import React, { Component } from 'react';
import { connect } from 'react-redux';
import { addAffidavit } from './../../redux/actions';


function Markup({ active, affidavits, people, addAffidavit }) {
  console.log({ active, affidavits, people, addAffidavit })
  addAffidavit();
  
  return <div>asd</div>
}


export class QuestionSection extends Component {
  constructor(...props) {
    super(...props);
  }

  render() {
    return <Markup {...this.props} />
  }
}


const mapStateToProps = (state, ownProps) => ({
  ...ownProps,
  affidavits: state.affidavits,
  people: state.people,
  active: state.active,
});


const mapDispatchToProps = (dispatch, ownProps) => ({
  ...ownProps,
  addAffidavit: () => dispatch(addAffidavit),
});


export default connect(mapStateToProps, mapDispatchToProps)(QuestionSection);


